.btn-style {
  color: lightyellow;
  background: teal;
  -webkit-transition: 0.3s color ease-in-out, 0.3s background ease-in-out;
  transition: 0.3s color ease-in-out, 0.3s background ease-in-out;
  margin: 20px 20px;
  border-radius: 5px;
}

.btn-style:hover {
  color: rgb(66, 43, 43);
  font-weight: bold;
  background: goldenrod;
  -webkit-transition: 0.3s color ease-in-out, 0.3s background ease-in-out;
  transition: 0.3s color ease-in-out, 0.3s background ease-in-out;
}

